'use client';

import { useState } from 'react';
import { createContent } from '@/services/content.service';

interface FormData {
  type: 'movie' | 'series' | 'program' | 'play' | 'kids' | 'podcast' | 'competition';
  title: string;
  description: string;
  year?: number;
  rating?: number;
  is_premium: boolean;
  category_ids: number[];
}

export default function CreateContentForm({ onSuccess }: { onSuccess: () => void }) {
  const [form, setForm] = useState<FormData>({
    type: 'movie',
    title: '',
    description: '',
    year: undefined,
    rating: undefined,
    is_premium: false,
    category_ids: [],
  });

  const handleChange = (e: any) => {
    const { name, value, type, checked } = e.target;

    let val: any = value;

    if (name === 'year' || name === 'rating') {
      val = value ? Number(value) : undefined; // ÿ™ÿ≠ŸàŸäŸÑ ÿßŸÑŸÇŸäŸÖÿ© ÿ•ŸÑŸâ number ÿ£Ÿà undefined
    }

    if (type === 'checkbox') {
      val = checked;
    }

    setForm({ ...form, [name]: val });
  };

  const submit = async (e: any) => {
    e.preventDefault();

    try {
      await createContent(form);
      onSuccess(); // ÿ•ÿπÿßÿØÿ© ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ ÿ£Ÿà ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑŸÇÿßÿ¶ŸÖÿ©
      setForm({
        type: 'movie',
        title: '',
        description: '',
        year: undefined,
        rating: undefined,
        is_premium: false,
        category_ids: [],
      });
    } catch (err) {
      console.error('ÿÆÿ∑ÿ£ ÿπŸÜÿØ ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ:', err);
      alert('ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿ≠ŸÅÿ∏ ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ.');
    }
  };

  return (
    <form onSubmit={submit} className="bg-gray-900 p-6 rounded-lg mb-6 text-right shadow-lg">
      <h3 className="text-xl font-bold mb-4 text-red-600">‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≠ÿ™ŸàŸâ ÿ¨ÿØŸäÿØ</h3>

      <select
        name="type"
        value={form.type}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
      >
        <option value="movie">ŸÅŸäŸÑŸÖ</option>
        <option value="series">ŸÖÿ≥ŸÑÿ≥ŸÑ</option>
        <option value="program">ÿ®ÿ±ŸÜÿßŸÖÿ¨</option>
        <option value="play">ŸÖÿ≥ÿ±ÿ≠Ÿäÿ©</option>
        <option value="kids">ÿ£ÿ∑ŸÅÿßŸÑ</option>
        <option value="podcast">ÿ®ŸàÿØŸÉÿßÿ≥ÿ™</option>
        <option value="competition">ŸÖÿ≥ÿßÿ®ŸÇÿ©</option>
      </select>

      <input
        name="title"
        placeholder="ÿßŸÑÿπŸÜŸàÿßŸÜ"
        value={form.title}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
        required
      />

      <textarea
        name="description"
        placeholder="ÿßŸÑŸàÿµŸÅ"
        value={form.description}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
      />

      <div className="grid grid-cols-2 gap-3">
        <input
          type="number"
          name="year"
          placeholder="ÿßŸÑÿ≥ŸÜÿ©"
          value={form.year ?? ''}
          onChange={handleChange}
          className="p-2 bg-gray-800 rounded text-white"
        />
        <input
          type="number"
          step="0.1"
          name="rating"
          placeholder="ÿßŸÑÿ™ŸÇŸäŸäŸÖ"
          value={form.rating ?? ''}
          onChange={handleChange}
          className="p-2 bg-gray-800 rounded text-white"
        />
      </div>

      <label className="flex items-center gap-2 mt-3">
        <input
          type="checkbox"
          name="is_premium"
          checked={form.is_premium}
          onChange={handleChange}
        />
        ŸÖÿ≠ÿ™ŸàŸâ ŸÖÿØŸÅŸàÿπ
      </label>

      <button
        type="submit"
        className="mt-4 w-full bg-red-600 py-2 rounded hover:bg-red-700 transition"
      >
        ÿ≠ŸÅÿ∏ ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ
      </button>
    </form>
  );
}
